<template>

    <div class="app-container">
        <!--  -->
        <el-scrollbar style="width:100%">
        <div class="container-query">
            <queryItem label="">
                <el-input v-model="queryContion.code" placeholder=""></el-input>
            </queryItem>
            <query-item isButton style="width:100%;padding-left:30px">
                <el-button type="primary"  @click="search" ></el-button>
                <el-button  plain @click="reset" ></el-button>
                <el-button  plain @click="handleAdd" ></el-button>
                <el-button  plain @click="handleDeleteBatch" type="danger"></el-button>
            </query-item>
        </div>
        <!--  -->
        <div class="container-table">
            <div class="common-table-style">
                <el-table :data="pageData.list" border
                 @selection-change="handleSelectionChange"
                >
                    <el-table-column type="selection"></el-table-column>
                    <el-table-column type="index" width="50" label=""></el-table-column>
                    <el-table-column prop="name"  label="">
                    </el-table-column>
                    <el-table-column prop="code"  label="">
                    </el-table-column>
                    <el-table-column  label="">
                      <template slot-scope="scope">
                            <el-button @click="handleEdit(scope.row)" type="text"></el-button>
                            <el-button @click="handleDelete(scope.row)" type="text" style="color:red"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
            </div>
            <div class="pagination">
              <pagination :page-list="pageData" @pagesearch="handlePage"></pagination>
            </div>
        </div>
        <faTableEdit ref="faTableEdit" @back="getData" :selectData="selectData"></faTableEdit>
        </el-scrollbar>
    </div>
</template>
<script>
import faTableEdit from "./edit"
export default {
    components: { faTableEdit },
    name: "faTableQuery",
    data() {
      return {
        // 
        pageData: {
          list: [],
          pageNumber: 1,
          pageSize: 10,
          totalCount: 0,
          totalPage: 0
        },
        // 
        queryContion: {
          code:"",
        },
        //
        selectData: {
        },
        //
        delVal:""
      }
    },
    created() {
      //
      this.getDropData()
      //
      this.getData()
    },
    methods:{
        //
        getDropData(){
        },
        // 
        empty(){
            this.queryContion = {
                code:"",
            }
        },
        //
        getData(row){
            let queryContion = {
                condition: this.queryContion,
                number: this.pageData.pageNumber,
                size: this.pageData.pageSize
            }
            this.$http.post("api/familyTree/faTable/query",queryContion)
            .then(res => {
              this.pageData.list = res.data.body.records;
              this.pageData.totalCount = res.data.body.total;
              this.pageData.totalPage = res.data.body.pages;
            })
        },

        // 
        search(){
            this.getData()
        },
        // 
        reset(){
            this.empty()
            this.getData()
        },
        //
        handlePage(number, size){
            this.pageData.pageNumber = number
            this.pageData.pageSize = size
            this.getData()
        },

        //
        handleEdit(row) {
          this.$refs.faTableEdit.openByEdit(row)
        },
        //
        handleDelete(row){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() =>
          {
              this.$http.delete("api/familyTree/faTable/deleteById/" + row.id)
              .then(res => {
                if(res.data.code == '200'){
                  this.$message.success(res.data.message)
                  this.getData()
                }
              })
          })
        },

        //
        handleAdd() {
          this.$refs.faTableEdit.openByNew()
        },
        handleDeleteBatch(){
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
          .then(() => {
            let deletebatch = this.$utils.findIds(this.delVal)
            this.$http.post("api/familyTree/faTable/batchDelete",deletebatch)
            .then(res => {
              if(res.data.code == '200'){
                this.$message.success(res.data.message)
                this.getData()
              }
            })
          })
        },
        //
        handleSelectionChange(val){
          this.delVal = val
        }
    }
}
</script>


